function MediumWidget(props) {
  ;(function () {
    function ls() {
      var div = document.createElement('div')
      div.id = 'medium-widget'
      var mediumWidget = document.createElement('script')
      mediumWidget.src = 'https://medium-widget.pixelpoint.io/widget.js'
      document.body.appendChild(div)
      document.body.appendChild(mediumWidget)
      mediumWidget.onload = createMediumWidget
    }
    function createMediumWidget() {
      var mediumWidgetParams = document.createElement('script')
      mediumWidgetParams.text =
        'MediumWidget.Init({renderTo: "' +
        props.renderTo +
        '",params: {"resource": "' +
        props.resource +
        '","postsPerLine": ' +
        props.postsPerLine +
        ',"limit": ' +
        props.limit +
        ',"picture": "' +
        props.picture +
        '","fields": ' +
        props.fields +
        ',"ratio": "' +
        props.ratio +
        '"}})'
      document.body.appendChild(mediumWidgetParams)
    }
    if (window.attachEvent) {
      window.attachEvent('onload', ls)
    } else {
      window.addEventListener('load', ls, false)
    }
  })()
  return true
}

module.exports = MediumWidget
